@import url('https://fonts.googleapis.com/css?family=Baloo+2:400,500,600,700,800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500;600;700;800&family=Baloo+Bhaina+2:wght@400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap');
@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700;800;900&display=swap');

$main-color: #004551;
$color2: #0ca890;
$background-color2: #f2f2f2;
$gradient: linear-gradient(to right, $main-color, $color2);
$fontColor: #222222;
$fontColor2: #474747;
$maxWidth: 1200px;
$shadow: #dbdbdb;

//elements
html {
  scroll-behavior: smooth;
}

.App {
  color: $fontColor;
  font-family: 'Nunito', sans-serif;
}

.page {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  text-align: center;
  box-sizing: border-box;
  padding-bottom: 50px;
}

.page-title {
  font-weight: 900;
  font-size: 60px;
  text-align: left;
  padding-top: 100px;
  text-transform: uppercase;
  color: #004551;
}

.container {
  box-sizing: border-box;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  max-width: $maxWidth;
  margin-left: auto;
  margin-right: auto;
}

h1 {
  padding: 20px;
  font-size: 2.5em;
  margin: 0px;
}

a {
  text-decoration: none;
  all: inherit;
  cursor: pointer;

  &:active {
    transform: scale(.95)
  }
}

.mobile {
  display: none;
}

//Navbar
.navbar {
  position: absolute;
  width: 100%;
  height: 80px;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  box-sizing: border-box;
  padding-left: 20px;
  padding-right: 20px;
}

//Header image
.image-header {
  display: flex;
  align-items: center;
  justify-content: start;
  background: transparent;
  width: 200px;
  padding: 0px;
  color: white;
  margin-left: -10px;
  transition: filter .2s;

  &:hover {
    cursor: pointer;
    filter: brightness(.7);
  }
}

.name {
  text-align: left;
  font-size: 12px;
  font-weight: 700;
}

.mike-image {
  width: 70px;
  clip-path: circle(35%);
}

//Contact button
.contact-label-container {
  height: 100%;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
}

.contact-label {
  width: 100px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 900;
  text-transform: uppercase;
  color: $color2;
  background-color: white;
  transition: color .3s, background-color .3s;
  right: 20px;

  &:hover{
    background: $color2;
    color: white;
    cursor: pointer;
  }
}

//title page
#title-page {
  padding-bottom: 0px;
  position: relative;
  //  background: $main-color;
  // min-height: 100vh;
  height: 100%;
  width: 100%;
  margin-bottom: 100px;
}

//test
.skew-container {
  position: absolute;
  height: 100vh;
  width: 100vw;
}

.skewed {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to left, $main-color, $color2);
  z-index: 0;
}

.skewed2 {
  position: absolute;
  top: -3%;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, $main-color, $color2);
  transform-origin: 0 0;
  transform: skewY(5deg);
  z-index: 0;
}

.svg-img {
  max-width: 1000px;
  width: 100%;
}

.title-container {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1600px;
  min-height: 90vh;
  padding-top: 100px;
  width: 100%;
  // min-height: 100vh;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
  z-index: 100;
}

.title-inner-container {
  width: 100%;
  height: 100%;
  // min-height: 70vh;
  box-sizing: border-box;
  padding: 30px;
  // position: relative;
  max-width: $maxWidth;
  z-index: 100;
}

.title-header {
  font-weight: 900;
  font-size: 60px;
  text-align: left;
  color: white;
  line-height: 150%;
  padding-bottom: 10px;
}

.title-subheader {
  color: white;
  font-weight: 700;
  font-size: 30px;
  text-align: left;
  display: block;
}

.mybg-subheader {
  width: 100%;
  box-sizing: border-box;
  padding: 0px 20px 40px 20px;
  margin-top: 5%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.mybg-wrapper {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 20px;
  box-sizing: border-box;
  margin-bottom: 20px;
}

.title-bar {
  width: 100%;
  height: 2px;
  background-color: $background-color2;
  margin: 5px;
  z-index: 10;
}

.mybg-header {
  text-align: left;
  color: white;
  font-weight: bold;
  margin-right: auto;
  font-size: 24px;
  z-index: 10;
}

.mybg-card {
  background-color: white;
  width: 45%;
  min-width: 150px;
  min-height: 400px;
  padding: 10px;
  box-sizing: border-box;
  text-align: left;
  margin: 10px;
  border-radius: 20px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.16);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
  overflow: hidden;


  .mybg-title {
    font-weight: 700;
    font-size: 25px;
    color: $main-color;
    padding: 10px;
    position: absolute;
  }
  
  .mybg-icon {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto auto;
    width: 40%;
    height: 40%;
    filter: invert(100%) brightness(.05) sepia(20) saturate(80) hue-rotate(540deg);
    transform: scale(1);
    transition: transform .4s;
  }
  
  .mybg-text {
    box-sizing: border-box;
    padding: 10px;
    font-size: 10px;
    transition: opacity .4s transform .4s;
    text-align: center;
    position: absolute;
    top: 100%;
    transform: translateY(0px);
    opacity: 0;
    transition: opacity .4s, transform .2s;
  }

  &:hover {
    .mybg-text {
      transform: translateY(-100%);
      opacity: 1;
    }

    .mybg-icon {
      transform: scale(.8) translateY(-20%);
    }
  }
}

@media only screen and (max-width: 1000px) {
  .title-container {
    padding-top: 100px;
    flex-wrap: wrap
  }

  .skewed2 {
    height: 100%;
  }
}


@media only screen and (max-width: 600px) {
  #title-page {
    margin-bottom: 0px;
  }

  .mybg-subheader {
    width: 100%;
  }

  .mybg-card {
    width: 100%;
    box-shadow: none;
    margin: 0px;
  }

  .hello-bubble {
    min-width: 130px;
    height: 40px;
  }

  .hello-bubble-text {
    font-size: 16px;
  }

  .hello-triangle {
    width: 40px;
    height: 40px;
    background-color: white;
    top: 50%;
    left: -15px;
    z-index: 4;
  }

  .title-container {
    padding: 0px;
  }

  .title-right {
    padding: 0px;
  }

  .skewed2 {
    position: absolute;
    top: -50%;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 150%;
  }

  .title-bar {
    width: 100%;
    height: 2px;
    background-color: $background-color2;
    margin: 0px;
    margin-top: 10px;
    margin-bottom: 30px;
    z-index: 10;
  }
}

//Work
#work {
  .page-title {
    padding-top: 0px;
  }
}

.work-container {
  position: relative;
  width: 100%;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;

  .work-logo-wrap {
    width: 60px;
    height: 60px;
    min-width: 60px;
    background-color: white;
    border-radius: 15%;
    overflow: hidden;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 0px 5px -2px grey;
    margin-right: 20px;
  }

  .work-logo {
    width: 100%;
    border-radius: 15%;
  }

  .work-bar {
    position: absolute;
    border-radius: 10px;
    margin-left: 20px;
    height: 100%;
    width: 20px;
    background: linear-gradient($main-color, $color2);
  }

  .job {
    width: 100%;
    display: flex;
    position: relative;
    
    .bullet {
      position: absolute;
      background-color: white;
      top: 40px;
      left: 10px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      z-index: 100;
      display: flex;
      align-items: center;
      justify-content: center;

      .bullet-inner {
        height: 80%;
        width: 80%;
        border-radius: 50%;
        background: linear-gradient($main-color, $color2);
      }
    }

    .role {
      margin-left: 70px;
      margin-top: 20px;
      width: 100%;
      box-sizing: border-box;
      padding: 10px;
      background-color: #fcfcfc;
      border-radius: 5px;
      border: 1px solid #dbdbdb;
      box-shadow: 0px 0px 10px 0px transparent;
      transition: border .5s, box-shadow .2s, background-color .4s;
      font-size: 16px;

        .position {
          line-height: 125%;
          font-weight: 900;
          font-size: 120%;
          text-align: left;
        }

        .company {
          font-size: 100%;
          font-weight: 900;
        }

        .time {
          font-size: 70%;
          color: grey;
        }
      
      ul {
        margin: 0px;
        padding-inline-start: 20px;
      }

      .description {
        text-align: left;
        opacity: 0;
        max-height: 0;
        transition: max-height .4s, opacity .3s ease-in-out;

        li {
          margin-bottom: 5px;
          font-size: 11px;
          line-height: 12px;
          margin-top: 0px;
          transition: margin-top 1s;
        }
      }

      &:hover {
        background-color: white;
        border: 1px solid transparent;
        box-shadow: 0px 0px 10px 0px #dbdbdb;
        // transition: border .5s, box-shadow .5s;

        .description {
          max-height: 500px;
          opacity: 1;

          li {
            margin-top: 15px;
            transition: margin-top .2s;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 600px) {
  .work-container {
    .work-bar {
      margin-left: 10px;
      height: 100%;
      width: 10px;
    }

    .work-logo-wrap {
      margin-right: 10px;
    }

    .bar-vert{
      margin-left: 10px;
      margin-right: 10px;
    }
  
    .job {
      .bullet {
        left: 0px;
        width: 30px;
        height: 30px;
        top: 45px;
      }

      .role {
        margin-left: 40px;
        font-size: 12px;

        .description {
          li {
              margin-top: 0px;
          }
        }
  
        &:hover {
          .description {
            li {
                margin-top: 10px;
            }
          }
        }
      }
    }
  }
}



//Projects
#projects {
  // background-color: $background-color2;
  text-align: left;
  padding-bottom: 40px;
  // min-height: 100vh;
  position: relative;
  // overflow: hidden;

  .page-title {
    color: white;
  }
}

.skewed3 {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 95%;
  background: linear-gradient(to right, $main-color, $color2);
  transform-origin: 0 0;
  transform: skewY(5deg);
  z-index: -1;
}

.project {
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  padding: 35px;
  padding-bottom: 50px;
  text-align: left;

  .project-title-container {
    box-sizing: border-box;
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 70px;
    width: 100%;
    margin-bottom: 20px;
  }

  .project-type {
    height: 80%;
    width: auto !important;
    margin-right: 10px;
  }

  .project-title-wrap {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center
  }

  .project-title {
    font-weight: 900;
    font-size: 36px;
    text-transform: uppercase;
  }

  .project-subheader {
    font-weight: 200;
    font-size: 12px;
    color: gray;
  }
  

  .sub-title {
    font-weight: 700;
  }

  .project-subheader {
    line-height: 125%;
    margin-top: auto;
  }

  .project-bottom {
    height: 100%;
    display: flex;
    flex-direction: row-reverse;
    flex-grow: 1;
    padding-top: 10px;
  }

  .project-left {
    width: 100%;
    flex-grow: 1;
    box-sizing: border-box;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .project-image {
    width: 100%;
    box-sizing: border-box;
    transition: transform .5s;
  
    &:hover {
      transform: scale(.95);
      transition: transform .5s;
    }
  }

  .project-right {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .project-stack {
    width: 100%;
    box-sizing: border-box;
    padding-bottom: 30px;
  }

  .project-description {
    width: 100%;
    line-height: 22px;
    padding-bottom: 30px;
    color: $fontColor2;
  }

  .icon-container {
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 10px;
  }
  
  #project-icon {
    width: 45px;
    max-height: 45px;
    object-fit: fill;
    padding: 3px;
    box-sizing: border-box;
    margin-right: 5px;
    margin-bottom: 5px;
    border-radius: 10px;
    background-color: $background-color2;
  }
  
  .description-link-wrapper {
    display: flex;
    justify-content: flex-start;
  }

  .description-link {
    margin-right: 10px;
    box-shadow: 0px 5px 5px transparent;
    transform: translateY(0px);
    transition: transform .4s, box-shadow .4s;

    img {
      height: 40px;
      width: auto !important;
    }

    &:hover {
      transform: translateY(-3px);
      box-shadow: 0px 5px 5px $shadow;
    }
  }

  .web-link {
    height: 35px;
    padding-left: 25px;
    padding-right: 25px;
    width: fit-content;
    background-color: $main-color;
    color: white;
    border-radius: 5px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

@media only screen and (max-width: 800px) {
  .project {
    .project-bottom {
      flex-direction: column-reverse;
    }

    .project-title {
      font-size: 30px;
    }

    .project-right {
      padding: 0px;
      box-sizing: border-box;
    }

    .project-image {
      padding: 10px;
      max-width: 40vh;
    }

    .project-left {
      padding: 0px;
    }
  }

}
  
@media only screen and (max-width: 600px) {
  .project {
    padding: 20px;
    padding-bottom: 50px;

    .project-title-container {
      margin-bottom: 5px;
      text-align: left;
      height: 50px;

    }

    .project-title-wrap {
      align-items: flex-start
    }

    .project-title {
      font-size: 20px;
    }

    .project-bottom {
      padding: 0px;
      padding-left: 10px;
      padding-right: 10px
    }

    .project-description {
      font-size: 12px;
      padding-bottom: 10px;
      line-height: 18px;
    }

    .project-stack {
      padding-bottom: 10px;
    }

    .project-type {
      height: 50%
    }

    #project-icon {
      height: 30px;
      width: 30px;
      margin-right: 3px;
      margin-bottom: 3px;
    }

    .description-link-wrapper {
      width: 100%;
      justify-content: center;
    }

    .description-link {
      margin-right: 10px;
  
      img {
        height: 35px;
        width: auto !important;
      }
    }
  }
}

@media only screen and (max-width: 350px) {
  .project {
    .project-title {
      font-size: 16px;
    }

    .project-subheader {
      font-size: 8px;
    }

    .sub-title {
      font-size: 13px;
    }

    .project-description {
      font-size: 10px;
      line-height: 14px;
    }

    .project-stack {
      padding-bottom: 0px;
    }
  }
}

.carousel .slide { 
  background-color: white !important;
}

.carousel .control-next.control-arrow:before {
  border-left: 8px solid $main-color !important;
}

.carousel .control-prev.control-arrow:before {
  border-right: 8px solid $main-color !important;
}

.carousel.carousel-slider {
  border-radius: 20px;
  overflow: hidden;
}

.carousel .control-dots .dot {
  background-color: $color2 !important;
}

.bar {
  width: 100%;
  height: 2px;
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: #dbdbdb;
  z-index: 10;
}

.bar-vert {
  width: 2px;
  height: 100%;
  background: $background-color2;
  margin-left: 20px;
  margin-right: 20px;
}



//Skills
#skills {
  // background-color:  $background-color2;
}

.skill-wrap {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
}

.skill-title {
  font-size: 20px;
  font-weight: 900;
  text-align: left;
  margin-left: 10px;
}

.skill-container {
  padding-top: 20px;
  padding-bottom: 20px;
  width: 100%;
}

.skill-icon-wrap {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.skill-icon {
  max-height: 75%;
  max-width: 75%;
  border-radius: 5px;
}

.skill-description {
  color: $main-color;
  position: absolute;
  font-weight: 700;
  font-size: 10px;
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  line-height: 110%;
  opacity: 0;
  transform: translateY(10px);
  transition: opacity .2s, transform .3s;
  user-select: none;
}

.skill-icon-container {
  height: 70px;
  width: 70px;
  background-color: white; 
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px;
  box-shadow: 0 0 2px transparent;
  transition: transform .5s;
  background-color: $background-color2;

  &:hover {
    transform: scale(1.1);
    box-shadow: 0 0 4px #dbdbdb;
    transition: transform .5s, box-shadow .2s;


    .skill-icon {
      filter: brightness(.4);
      opacity: .2;
    }

    .skill-description {
      opacity: 1;
      transform: translateY(0px);
      transition: opacity .3s, transform .3s;
    }
  }
}


//Certifications
#certifications {
 // background-color: $background-color2
}

.certification {
  width: 90%;
  height: 120px;
  border: 1px solid #dbdbdb;
  background-color: #fcfcfc;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  transform: scale(1);
  transition: transform .2s, box-shadow .2s, border .2s, background-color .2s;

  &:hover {
    transform: scale(1.01);
    border: 1px solid transparent;
    box-shadow: 0 0 10px #dbdbdb;
    background-color: white;
    transition: transform .2s, box-shadow .2s, border .2s, background-color .2s;
  }
}

.certification-content {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: minmax(330px, 55%) auto 85px;;
  align-items: center;
}

.cert-img-wrap {
  height: 120px;
  width: 120px;
  padding-left: 10px;
  padding-right: 10px;

  display: flex;
  align-content: center;
  justify-content: center;
  margin-top: auto;
  margin-bottom: auto;
}

.cert-img {
  width: 100px;
  height: 100px;
  border-radius: 10px;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  margin-right: auto;
}

.certification-header-wrap {
  padding-left: 10px;
  padding-right: 10px;
}

.certification-header {
  font-weight: 700;
  font-size: 20px;
  padding-bottom: 10px;
}

.certification-subheader {
  font-weight: 200;
  font-size: 12px;
}

.certification-content-list {
  font-size: 10px;
  padding-left: 10px;
  text-align: left;
  padding-left: 20px;
}

.calendar-icon {
  width: 25px;
  height: 25px;
  margin-top: -8px;
}

.calendar-container {
  display: flex;
  align-items: center;
  margin-bottom: auto;
  margin-top: 15px;
  color: $color2;
  font-size: 18px;
  font-weight: 600;
  line-height: 0%;
}

.calendar-icon {
  margin-right: 5px;
  filter: invert(100%) brightness(.03) sepia(20) saturate(80) hue-rotate(540deg) brightness(3.5)
}

@media only screen and (max-width: 859px) {
  .certification-content-list {
    display: none;
  }

  .certification-content {
    grid-template-columns: minmax(330px, 90%) 85px;
  }
}

@media only screen and (max-width: 650px) {
  .certification {
    width: 100%;
    align-content: center;
  }

  .certification-header {
    font-size: 16px;
  }

  .mybg-subheader {
    padding: 5px;
    padding-top: 50px;
  }

  .certification-subheader {
    font-size: 10px;
  }

  .calendar-container {
    display: none;
  }
  
  .certification-content {
    grid-template-columns: 100%;
    height: 100%;
  }
}


//Contact
#contact {
  min-height: 100vh;
  height: 100%;
  width: 100%;
  display: inline-block;
  padding-bottom: 0;
  position: relative;
  overflow: hidden;
}

.contact-form {
  width: 100%;
  height: 50vh;
  text-align: center;
  margin-bottom: auto;
}

.contact-el {
  width: 90%;
  min-width: 200px;
  max-width: 500px;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 2px;
  border: none;
  box-shadow: 0 0 5px gray;
  font-family: 'Roboto', sans-serif;


  &:focus {
    outline: 2px solid $color2
  }
}

textarea {
  //height: 10vh;
  //flex-grow: 1;
  height: calc(70% - 100px);
  min-height: 25px;
  resize: none;
  font-family: 'Roboto', sans-serif;

  ::placeholder {
    font-family: 'Roboto', sans-serif
  }
}

.contact-button {
  margin-left: auto;
  margin-right: auto;
  width: 150px;
  background-color: transparent;
  border: 2px solid white;
  border-radius: 10px;
  color: white;
  font-weight: 700;
  padding: 10px;

  &:hover {
    cursor: pointer;
    background-color: white;
    color: $main-color
  }
}

.skewed-footer {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, $main-color, $color2);
  z-index: -4;
  transform: skewY(5deg);
  transform-origin: 0 0;
  overflow:hidden;
}


//Footer 
#footer {
  width: 100%;
  color: white;
  box-sizing: border-box;
  padding-top: 15px;
  padding-bottom: 15px;
  box-shadow: 0 0 10px white;
  margin-top: auto;
  position: absolute;
  bottom: 0;
}

.footer-links {
  display: flex;
  justify-content: space-around;
  max-width: 300px;
  margin-left: auto;
  margin-right:auto;
}

.footer-link {
  padding: 3px;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-content: center;
  position: relative;

  .hover-bar {
    width: 0%;
    height: 1px;
    background-color: white;
    position: absolute;
    bottom: 0px;
    left: 0px;
    transition: width .4s;
  }

  .footer-img {
    max-width: 80%;
    max-height: 80%;
    filter: invert(100%);
    transition: transform .4s;
  }

  &:hover {
    .hover-bar {
      width: 100%;
    }

    .footer-img {
      transform: translateY(-2px)
    }
  }
}

.copyright{
  text-align: center;
  margin-top: 10px;
}


@media screen and (max-width: 600px) {
  .mobile {
    display: block;
  }

  .title-header {
    font-size: 40px;
  }

  .title-subheader {
    font-size: 20px;
  }

  #work {
    margin-top: 50px;
  }

  .page-title {
    padding-top: 50px;
    font-size: 30px;
  }

  //Skills
  .skill-wrap {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }

  .skill-icon-container {
    width: 50px;
    height: 50px;
  }

  .skill-description {
    font-size: 8px;
    width: 50px;
    height: 50px;
  }

  .skill-icon-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around
  }

  //Footer
  #footer {
    padding-top: 20px;
    padding-bottom: 30px;
  }
}
  
//transition 
.fade-in {
  opacity: 0;
  transition: opacity .5s;
}

.fade-in.appear {
  opacity: 1;
}

.fade-in-slow {
  opacity: 0;
  transition: opacity 1s;
}

.fade-in-slow.appear {
  opacity: 1;
}



//Mobile Break
@media screen and (max-width: 450px) {
  .container {
    padding-left: 12px;
    padding-right: 12px;
  }
}


@media screen and (max-width: 340px) {
  .page-title {
    font-size: 2.2em;
  }
}


